import musicResourecs from './musicResourecs.js';

let audio;
export default {
	state:{
		num:1,
		playStatus:false
	},
	getters:{
		
	},
	mutations:{
		//
		addAudioEvent(state){
			audio.onPlay(()=>{
				state.playStatus = true
				console.log("");
			})
			audio.onPause(()=>{
				state.playStatus = false
				console.log("");
			})
			audio.onStop(()=>{
				console.log("");
			})
			audio.onEnded(()=>{
				console.log("");
			})
			audio.onTimeUpdate(()=>{
				console.log("");
			})
			audio.onError(()=>{
				console.log("");
			})
		},
		//
		destroy(){
			audio.offCanplay()
			audio.offPause()
			audio.offStop()
			audio.offEnded()
			audio.offTimeUpdate()
			audio.offError()
		},
		//
		audioPlay(){
			audio.src = musicResourecs.musicResourecs[1].src;
			audio.play()
		},
		// 
		audioPause(){
			audio.pause()
		}
	},
	actions:{
		init({commit}){
			// api
			if(audio) return
			audio = uni.createInnerAudioContext()
			commit('addAudioEvent')
		},
		playOrpause({ state,commit }){
			if(!state.playStatus){
				commit('audioPlay')
			}else{
				commit('audioPause')
			}
		}
	}
}